/* Internship.css */

.internship-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 20px;
  width: 100%;
  justify-content: center;
  overflow-x: auto;


}

.header h1{
  margin: 0;
  font-size: 35px;
  background-color: black;
  color: #fff;
  padding: 40px;
  font-family: 'Poppins', sans-serif;
}

.internship-scroll-container {

  display: flex; /* Enable horizontal scrolling */
  gap: 20px; /* Adjust the gap between cards */
  width: 80%;
  height: 30rem;
  justify-content: center;
  margin-left: 32rem;

}

.internship-card {
  flex: 0 0 auto; /* Fix the card size */
  display: flexbox;
  flex-direction: row;
  border: 1px solid #ddd;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  background-color: #fff;
  width: 25%;
  margin-top: 20px;
  margin-bottom: 20px;

}

.internship-card p
{
  font-size: 0.7rem;
  text-align: center;
}

.internship-card h2{
  font-size: 1rem;
}

.internship-card img {
  max-width: 100%;
  margin: 0;
  margin-bottom: -80px;
}
