/* home2.css */

.feature-section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #3b3a3a;
    color: #fff;
    padding: 60px;
    font-size: 20px;
  }
  
  .feature-content {
    flex: 1;
  }
  
  .feature-content h2 {
    margin: 25px;
    font-size: 30px;
    margin-top: -50px;
    text-align: justify;
  }
  
  .feature-list {
    margin: 25px;
    font-size: 18.5px;
    list-style-type: none;
    padding-left: 0;
    text-align: justify;
  }
  
  .feature-item {
    margin-bottom: 10px;
  }
  
  .feature-bullet {
    color: #ffc107; /* Yellow color for bullet points */
    margin-right: 10px;
    font-size: 18px;
  }
  
  .feature-image img {
    width: 100%;
    max-width: 400px;
    height: auto;
  }
  