.services2-container {
  text-align: center;
  padding: 20px;
}

.services2-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.service2-row {
  display: flex;
  justify-content: space-evenly;
  flex-basis: calc(70%); /* Three cards in a row */
  margin-bottom: 15px;

  
}
.service2-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  max-width: 100%;
  flex: 2;
  padding: 60px;
  background-color: white; /* Dark background color */
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  text-align: center;
  color: black;
  margin-top: 35px;
  margin-bottom: 5px;
  margin-right: 30px;
  margin-left: 30px;
  height: 400px;
  width: 300px;
}

.service2-card h3 {
  margin-top: 0;
}

.service2-card h3, p, a {
  margin: 10px;
}

/* Add additional styling as needed */
.apply-button {
  display: block;
  background-color: black;
  color: white;
  text-align: center;
  text-decoration: none;
  padding: 10px;
  border-radius: 5px;
  transition: background-color 0.3s;
}

.apply-button:hover {
  background-color: #ffbb00; /* Dark yellow on hover */
}


.service2-card1, 
.service2-card2,
.service2-card3
{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  max-width: 100%;
  flex: 2;
  padding: 60px;
  background-color: white; /* Dark background color */
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  text-align: center;
  color: black;
  margin-top: 35px;
  margin-bottom: 5px;
  margin-right: 30px;
  margin-left: 30px;
  height: 400px;
  width: 300px;
}

.service2-card1 a, 
.service2-card2 a
{
  margin-bottom: 20px;
  margin-top: 10px;
}

.service2-card1 p, 
.service2-card2 p
{
  margin-top: 40px;
}

.service2-card1 h3, 
.service2-card2 h3
{
  margin-bottom: 70px;
  margin-top: 20px;
}

.service2-card3 h3 {
  margin-bottom: 40px;
}
.service2-card3 p {
  margin-top: -35px;
  margin-bottom: 40px;
}

.service2-card3 a {
  margin-top: 5px;
  margin-bottom: 20px;
}

.service2-card2 h3 {
  margin-bottom: 75px;
  margin-top: 55px;
}
.service2-card2 p {
  margin-top: 45px;
  margin-bottom: 45px;
}

.service2-card2 a {
  margin-top: 0px;
  margin-bottom: 55px;
}

@media (max-width: 768px) {
  .service2-card, .service2-card1, .service2-card2, .service2-card3 {
    margin: 0px auto;
    margin-bottom: 10px;
    width: 50%;
  }

  .service2-row {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    padding: 0 auto;
  }

  .services2-list {
    max-width: 100%;
    display: flex;
    flex-direction: column;
  }

  .service2-card a, p, h3
  {
    width: 100%;
  }

  .service2-card h3 ,
  .service2-card1 h3,
  .service2-card2 h3,
  .service2-card3 h3
  {
    margin-top: -40px;    
  }

  .service2-card  p
  {
    margin-top: 0px;
    font-size: 80%;
    text-align: center justify;
  }

  .service2-card1 p,
  .service2-card2 p
  {
    font-size: 80%;
    margin-top: 15px;
    margin-bottom: 15px;
  }

  .service2-card2 p{
    margin-bottom: 15px;
  }

  .service2-card3 {
    margin-bottom: 0px;
  }

  .service2-card1 h3 ,
  .service2-card2 h3 
  {
    margin-bottom: 30px;
  }
  .service2-card3 h3 
  {
    margin-bottom: 20px;
  }

  .service2-card3 p {
    margin-top: 0px;
    font-size: 80%;
    margin-bottom: 15px;
    margin-top: 0px;
    font-size: 80%;
    text-align: center justify;

  }
  .service2-card a ,
  .service2-card1 a ,
  .service2-card2 a ,
  .service2-card3 a
  {
    margin-top: 5px;
    margin-bottom: -45px;
  }

  .services2-container {

    padding: 0px auto; /* Reduce container padding on mobile */
  }

  .services2-container p {
    margin-bottom: 25px;
  }
}