/* InternshipSection.css */

.internship-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    padding: 20px;
    background-color: #f1fb33;
  }
  
  .banner img {
    width: 50rem;
    height: auto;
    border-radius: 10px;
    background-color: rgba(250, 244, 244, 0.748);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
  }

  .intern-image{
    width: 100%;
  }
  .IMG {
    width: 80%;
  }

  .content {
    display: flex;
    flex-direction: row;
    text-align: center;
  }
  
  h2 {
    font-size: 24px;
    margin-bottom: 20px;
  }
  
  p {
    font-size: 16px;
    line-height: 1.5;
  }
  
  .domains {
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 20px;
  }
  
  .domain {
    padding: 20px;
  }
  
  h3 {
    font-size: 20px;
    margin-bottom: 10px;
  }
  
  /* Add more CSS styles as needed */
  