/* VerificationSection.css */

.verification-section {
    align-items: center;
    text-align: center;
    padding: 20px;
    background-color: #f5f5f5; /* Customize the background color */
  }
  
  .verification-banner img {
    max-width: 100%;
    height: auto;
    margin-bottom: 20px;
  }
  
  .verification-content {
    max-width: 800px; /* Customize the content width */
    margin-bottom: 20px;
  }
  
  .verification-animation {
    width: 100%;
    max-width: 400px; /* Customize the animation width */
  }
  