.form {
  width: 25%;
  margin: 0 auto;
  margin-top: 1rem;
  margin-bottom: 1rem;
  padding: 6rem;
  background-color: #f9f9f9;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.form h2 {
  text-align: center;
  margin-top: -3rem;
  margin-bottom: 1.5rem;
}

.form form {
  display: flex;
  flex-direction: column;
  width: 230%;
}

.form input {
  margin-bottom: 15px;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.form select
{
  width: 42%;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-left: 16px;
  margin-top: 15px;
  margin-bottom: 15px;
}

input[type="email"] {
  margin:15px;
  width: 40%;
}

.form button {
  margin-bottom: -80px;
  margin-top: 20px;
  margin-left: 15px;
  font-size: 16px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  max-width: 20%;
}

.form button:hover {
  background-color: #45a049;
}


.submit h2 {
  margin-bottom: -50px;
}

.submit button {
  margin-top: -20px;
  background-color: green;
  color: white;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s;
  padding: 5px;
  padding-top: 7.5px;
  padding-bottom: 7.5px;
  padding-right: 7px;
  padding-left: 7px;;
}

.submit button:hover {
  background-color: black;
}

@media screen and (max-width: 768px) {
  .form {
    width: 24%;
  }

  .form h2 {
    font-size: 100%;
    text-align: justify;
    margin-left: -60px;
    margin-right: 0px;
  }

  .form form input {
    padding: 5px;
    margin: 5px;
    width : 200px;
    margin-left: -60px;
    margin-right: 20px;
    
  }

  .form form select {
    margin-left: -60px;
    width: 210px;
  }

  .form select {
    margin: 5px;
    padding: 5px;
  }

  .form button {
    max-width: 40%;
    margin-left: -60px;
    padding: 10px;
  }
}

