/* Reset some default browser styles */

/* Global styles */
body {
  font-family: Arial, sans-serif;
  background-color: #f7f7f7;
  color: #333;

}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

/* Header styles */
header {
  margin-top: -50px;
  background-color: #ffcc00;
  padding: 20px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-logo {
  display: flex;
  align-items: center;
  gap: 10px;
  text-decoration: none;
  color: #333;
  font-weight: bold;
}

.header-logo img {
  width: 30px;
  height: 30px;
}

.nav-menu {
  list-style: none;
  display: flex;
  gap: 20px;
}

.nav-menu-item {
  font-weight: bold;
}

/* Hero section styles */
.hero {
  padding-left: 5rem;
  padding-right: 5rem;
  padding-top: 20rem;
  padding-bottom: 20rem;
}


/* Hero Container */
.hero {
  background-size: cover; /* Set background image to cover the container */
  background-repeat: no-repeat;
  background-position: center center; /* Center the background image */
  color: white;
  text-align: center;
  margin-bottom: 0px;
}


.hero h1 {
  font-size: 40px;
  margin-bottom: 20px;
  color: #333;
  margin-top: -100px;
  padding-bottom: 100px;
}

.hero p {
  font-size: 18px;
  margin-top: -100px;
  color: #666;
  margin-bottom: 40px;
  padding-bottom: 100px;
}

.cta-buttons {
  margin-top: -100px;
  display: flex;
  justify-content: center;
  gap: 20px;
  padding-bottom: 110px;
}

.cta-button {
  background-color: #090908;
  color: #fefbfb;
  font-size: 18px;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.cta-button:hover {
  background-color: #e6b800;
}

/* Why Choose Us section styles */
.why-choose-us {
  background-color: #fbf9f9;
  padding: 30px;
  text-align: justify;
  padding-left: 45px;
  padding-right: 45px;
  font-size: 1.8vh;
  max-width: 100%;
}

.why-choose-us li {
    list-style: none;
}

.why-choose-us h2 {
  font-size: 3.2vh;
  margin-bottom: 20px;
  color: #333;
}

.bullet {
  font-size: 2.6vh;
  margin-right: 10px;
  color: #ffcc00;

}

.why-choose-us {
    display: flex;
    justify-content: space-between; /* Adjust as needed */
    align-items: center; /* Align vertically centered */
  }
  
  
  .why-choose-image img {
    height:max-content;
    width: fit-content;
    max-height: 100%;
    max-width: 100%; /* Ensure the image doesn't exceed its container width */
  }
  